.App {
  text-align: center;
}

.App-logo {
  height: 275px;
  width: 275px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.flex-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.select-placeholder {
  opacity: 0.5;
}

a {
  text-decoration: none;
}

.alert-radio-label {
  text-transform: capitalize;
}